// = WORKS
//------------------------------------------------------------

.index-item-works{
    padding:6rem 0;
    .display-3 {
        font-family: var(--font-second);
        font-weight: var(--black);
    }
}

.card-deck {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(1fr, 1fr));
    @include sm {grid-template-columns: repeat(auto-fit, minmax(540px, 1fr));}
    @include min-md {grid-template-columns: repeat(auto-fit, minmax(345px, 1fr));}
    @include xl {grid-template-columns: repeat(auto-fit, minmax(555px, 1fr));}
    grid-gap: 1.5rem;
    .card {
        margin-left:0;
        margin-right:0;
        .card-title{
            font-family: var(--font-second);
            font-weight: var(--bold);
        }
    }
}