//------------------------------------------------------------
// = LAYOUTS
//------------------------------------------------------------

body {
    font-family: var(--font);
}
a {
    color:var(--color-turuncu);
    &:hover, &:focus {
        color: var(--color-mor);
        text-decoration: none;
    }
    svg{
        path {
            fill:var(--color-turuncu);
        }
        &:hover, &:focus{
            path {
                fill:var(--color-mor);
            }
        }
    }
}
.playFair {
    font-family: var(--font-second);
}