//------------------------------------------------------------
// = HERO
//------------------------------------------------------------

.index-item-hero {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh; //calc - header olmalı
    background: url($layout + "EVR_4188_PhotoEvrenCaglayan.jpg") bottom center no-repeat;
    background-size: cover;

    h1{
        font-family: 'Playfair Display', serif;
        color:$white;
        font-weight: 900;
        font-size: 4rem;
        @include max-sm {font-size:3rem}
        span{
            display: block;
            font-family: 'Roboto', sans-serif;
            font-weight: 300;
            margin-top:15px;
            font-size:1.7rem;
            @include max-sm {font-size:1.3rem}
        }
    }

    h2{
        //margin-top: 2rem;
        //margin-bottom: -2rem;
        text-align: right;
        font-weight: 100;
        color: $white;
        font-size:1.7rem;
        @include max-sm {font-size:1.3rem}
    }
}