//------------------------------------------------------------
// = HEADER
//------------------------------------------------------------

.index-header {
    z-index: 9;
    &.back-gray{background-color: rgba(200,200,200,.5);}
    nav.navbar{
        padding-left:0;
        .navbar-collapse {
            z-index: 9;
        }
        .bg-black {
            background: var(--gradient-2) !important;
        }
        ul.navbar-nav{
            padding: .5rem 1rem;
            .nav-link{
                color:$white;
                font-weight: var(--bold);
                font-family: var(--font-second);
            }
        }
    }
    button.navbar-toggler {
        border: none;
        border-color: transparent;
        outline: none;
        padding-left: 0;
    }
}