html {
  //------------------------------------------------------------
  // = GENERAL
  //------------------------------------------------------------
  --font:         'Roboto', sans-serif;
  --font-second:  'Playfair Display', serif;
  --black:        900;
  --bold:         700;
  --semi-bold:    600;
  --medium:       500;
  --regular:      400;
  --light:        300;

  --transition: all .15s ease;
  --radius:     6px;


  //------------------------------------------------------------
  // = SIZE
  //------------------------------------------------------------


  //------------------------------------------------------------
  // = COLOR
  //------------------------------------------------------------
  --color-bg:         #FBFBFB;

  --color-primary:    #E9002D;

  --color-paragraph:  #090b10;

  --color-highlight:  #FFE500;
  --color-success:    #36B37E;
  --color-alert:      #FF5630;

  --color-white:      #FFFFFF;
  --color-black:      #090b10;

  --color-turuncu:    #ff8500;
  --color-mor:        #d100ff;

  --gradient: linear-gradient(135deg, #ff8500 0%, #d100ff 100%);
  --gradient-2: linear-gradient(-176deg, #ff8500 0%, #d100ff 100%);

}

//------------------------------------------------------------
// = SCSS
//------------------------------------------------------------
$layout:              "../images/layout/";